import React, { useEffect, useContext, useState } from 'react';
import styles from './Templates.module.css';
import TemplateList from './TemplateList/TemplateList';
import Spinner from '../../../components/Spinner/Spinner';
import UserContext from '../../../contexts/UserContext';

const Templates = ({ getTemplates, templates }) => {
  const { userDispatch } = useContext(UserContext);
  const [current, setCurrent] = useState('landscape');

  useEffect(() => {
    if (!templates) getTemplates();
  }, [templates, getTemplates]);

  return (
    <div className={styles.Templates}>
      <ul className={styles.TemplateTypes}>
        <li
          className={[
            styles.TemplateType,
            current === 'landscape' ? styles.Current : null,
          ].join(' ')}
          onClick={() => setCurrent('landscape')}
        >
          Landscape
        </li>
        <li
          className={[
            styles.TemplateType,
            current === 'square' ? styles.Current : null,
          ].join(' ')}
          onClick={() => setCurrent('square')}
        >
          Square
        </li>
        <li
          className={[
            styles.TemplateType,
            current === 'vertical' ? styles.Current : null,
          ].join(' ')}
          onClick={() => setCurrent('vertical')}
        >
          Vertcal
        </li>
      </ul>
      {!templates ? (
        <Spinner
          style={{
            width: '80px',
            height: '80px',
            marginTop: '50px',
          }}
        />
      ) : (
        <TemplateList
          templates={templates}
          dispatch={userDispatch}
          current={current}
        />
      )}
    </div>
  );
};

export default Templates;
