import React, { useEffect, useState, useCallback, useRef } from 'react';
import styles from './Project.module.css';
import useUpdateProject from '../../../../hooks/useUpdateProject';
import Spinner from '../../../../components/Spinner/Spinner';
import { Link } from 'react-router-dom';
import useDeleteVideo from '../../../../hooks/useDeleteVideo';
import whiteDot from '../../../../assets/homepage/whiteDot.svg';

const Project = ({ video, setShouldFetch }) => {
  const [input, setInput] = useState(video.title || '');
  const [isDropdown, setIsDropdown] = useState(false);
  const { isUpdatingProject, updateProject, updateResponse } =
    useUpdateProject();
  const { isDeletingVideo, deleteVideoResponse, deleteVideo } =
    useDeleteVideo();
  const inputRef = useRef(null);
  const transformDate = (d) => {
    let date = new Date(d);
    let day = date.getDate();
    let month = date.getMonth() + 1;
    let year = date.getFullYear();
    let dateString = `${day > 9 ? day : '0' + day}.${
      month > 9 ? month : '0' + month
    }.${year}`;
    return dateString;
  };

  const blurHandler = (e) => {
    inputRef.current.disabled = true;
    let value = e.target.value.trim();
    if (value.length > 0 && value !== video.title) {
      updateProject({
        id: video._id,
        title: value,
      });
    } else setInput(video.title);
  };

  const deleteClickHanlder = () => {
    let box = window.confirm('Are you sure you want to delete this project?');
    if (box === true) deleteVideo(video._id);
  };

  const renameClickHanlder = () => {
    setIsDropdown(false);
    inputRef.current.disabled = false;
    inputRef.current.focus();
  };

  useEffect(() => {
    if (inputRef.current) inputRef.current.disabled = true;
  }, []);

  const clickHandler = useCallback((e) => {
    for (let i = 0; i < e.path.length; i++) {
      if (e.path[i].classList && e.path[i].classList[0] === 'projectDropdown') {
        return;
      }
    }
    setIsDropdown(false);
  }, []);

  useEffect(() => {
    if (isDropdown) document.addEventListener('click', clickHandler);
    return () => document.removeEventListener('click', clickHandler);
  }, [isDropdown, clickHandler]);

  useEffect(() => {
    if (updateResponse && typeof updateResponse !== 'string') {
      video.title = updateResponse.title;
    } else if (updateResponse === 'error') setInput(video.title);
  }, [updateResponse, video]);

  useEffect(() => {
    if (deleteVideoResponse === 'success') {
      setShouldFetch(true);
    }
  }, [deleteVideoResponse, setShouldFetch]);

  return (
    <div className={styles.Project}>
      <Link
        to={`/${!video.final_video_url ? 'editor' : 'preview'}/${video._id}`}
        className={styles.Video}
        style={{ backgroundImage: `url(${video.screenshot_url})` }}
      ></Link>

      <div className={styles.InfoContainer}>
        <div className={styles.Info}>
          <div className={styles.DateAndName}>
            {isUpdatingProject ? (
              <Spinner
                style={{ width: '20px', height: '20px', margin: 'auto 0' }}
              />
            ) : (
              <input
                className={styles.Name}
                type='text'
                value={input}
                onBlur={blurHandler}
                onChange={(e) => setInput(e.target.value)}
                ref={inputRef}
                disabled
              />
            )}
            <p className={styles.Date}>{transformDate(video.created_at)}</p>
          </div>
          {isDeletingVideo ? (
            <Spinner
              style={{
                width: '25.6px',
                height: '25.6px',
                marginRight: '5px',
              }}
            />
          ) : (
            <div className={styles.OptionsContainer}>
              <div
                className={styles.Options}
                onClick={() => setIsDropdown((prevState) => !prevState)}
              >
                <div className={styles.Dots}>
                  <img src={whiteDot} alt='dot' />
                  <img src={whiteDot} alt='dot' />
                  <img src={whiteDot} alt='dot' />
                </div>
              </div>
              {isDropdown ? (
                <ul className={['projectDropdown', styles.Dropdown].join(' ')}>
                  <li
                    className={styles.DropdownOption}
                    onClick={deleteClickHanlder}
                  >
                    Delete
                  </li>
                  <li
                    className={styles.DropdownOption}
                    onClick={renameClickHanlder}
                  >
                    Rename
                  </li>
                </ul>
              ) : null}
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Project;
