/* eslint-disable jsx-a11y/img-redundant-alt */
import React, { useContext } from 'react';
import styles from './DesignControls.module.css';
import EditorContext from '../../../contexts/EditorContext';
import Backdrop from '../../../components/Backdrop/Backdrop';
import ImageDropdown from '../ImageDropdown/ImageDropdown';
import ShapeDropdown from '../ShapeDropdown/ShapeDropdown';
// import SubtitlesDropdown from '../SubtitlesDropdown/SubtitlesDropdown';
import { ReactComponent as AddText } from '../../../assets/editor/addText.svg';
import { ReactComponent as AddImage } from '../../../assets/editor/addImage.svg';
import { ReactComponent as AddShape } from '../../../assets/editor/addShape.svg';
import { ReactComponent as Subtitles } from '../../../assets/editor/subtitles.svg';

const DesignControls = ({ canvas, isWords, assets, setAssets }) => {
  const { editorState, editorDispatch } = useContext(EditorContext);

  const clickHandler = (e) => {
    if (
      canvas &&
      !editorState.shouldCropMedia &&
      !editorState.isLoadingTemplate
    ) {
      let type = '';
      let id = e.currentTarget.id;
      if (id === 'addText') type = 'setShouldAddText';
      else if (id === 'addImage') type = 'setIsImageDropdown';
      else if (id === 'addShape') type = 'setIsShapeDropdown';
      else if (id === 'subtitles' && isWords) {
        // type = 'setIsSubtitlesDropdown';
        editorDispatch({
          type: 'setIsSubtitles',
          data: !editorState.isSubtitles,
        });
      }
      if (type.length > 0) editorDispatch({ type, data: true });
      if (editorState.isCropMode) {
        editorDispatch({ type: 'setShouldCropMedia', data: true });
      }
    }
  };

  return (
    <div className={styles.ControlsContainer}>
      <div
        className={styles.AddTextContainer}
        id='addText'
        onClick={clickHandler}
      >
        <AddText className={styles.AddIcon} />
        <div className={styles.HoverContent}>
          <div className={styles.HoverTriangle}></div>
          <p className={styles.HoverText}>Add text</p>
        </div>
      </div>
      <div className={styles.DesignControl}>
        <div
          className={styles.AddImageContainer}
          id='addImage'
          onClick={clickHandler}
        >
          <AddImage
            className={[
              styles.AddIcon,
              editorState.isImageDropdown ? styles.ActiveDesignControl : null,
            ].join(' ')}
          />
          <div className={styles.HoverContent}>
            <div className={styles.HoverTriangle}></div>
            <p className={styles.HoverText}>Add image</p>
          </div>
        </div>
        {editorState.isImageDropdown ||
        editorState.shouldReplaceImage ||
        editorState.shouldAddCanvasBgImage ? (
          <>
            <Backdrop />
            <div className={styles.ImageDropdown}>
              <ImageDropdown assets={assets} setAssets={setAssets} />
            </div>
          </>
        ) : null}
      </div>

      <div className={styles.DesignControl}>
        <div
          className={styles.AddShapeContainer}
          id='addShape'
          onClick={clickHandler}
        >
          <AddShape
            className={[
              styles.AddIcon,
              editorState.isShapeDropdown ? styles.ActiveDesignControl : null,
            ].join(' ')}
          />
          <div className={styles.HoverContent}>
            <div className={styles.HoverTriangle}></div>
            <p className={styles.HoverText}>Add shape</p>
          </div>
        </div>
        {editorState.isShapeDropdown ? (
          <>
            <Backdrop />
            <div className={styles.ShapeDropdown}>
              <ShapeDropdown />
            </div>
          </>
        ) : null}
      </div>
      <div className={styles.DesignControl}>
        <div
          className={styles.AddShapeContainer}
          id='subtitles'
          onClick={clickHandler}
        >
          <Subtitles
            className={[
              styles.AddIcon,
              editorState.isSubtitles ? styles.ActiveDesignControl : null,
            ].join(' ')}
          />
          <div className={styles.HoverContent}>
            <div className={styles.HoverTriangle}></div>
            <p className={styles.HoverText}>Subtitles</p>
          </div>
        </div>
      </div>
      {/* {editorState.isSubtitlesDropdown ? (
        <>
          <Backdrop />
          <div className={styles.SubtitlesDropdown}>
            <SubtitlesDropdown isWords={isWords} />
          </div>
        </>
      ) : null} */}
    </div>
  );
};

export default DesignControls;
