import { useEffect, useCallback } from 'react';
import {
  undoDeletedText,
  undoRestoredText,
  undoChangedText,
} from '../utils/undoText';

const undoHistory = [];
const updateUndoHistory = (lastAction) => undoHistory.push(lastAction);

const useUndo = (
  state,
  dispatch,
  videoRef,
  words,
  setCurrentWordIndex,
  setCurrentSelection,
  setVideoCuts,
  setPlayerTime,
  setShouldRerenderSub,
  subList
) => {
  const handleTextUndo = useCallback(
    (prev, wordsArr) => {
      if (prev.action === 'delete') {
        undoDeletedText(
          prev.target,
          wordsArr,
          setCurrentWordIndex,
          setCurrentSelection,
          setVideoCuts,
          setPlayerTime
        );
      } else if (prev.action === 'restore') {
        undoRestoredText(
          prev.target,
          wordsArr,
          setCurrentWordIndex,
          setCurrentSelection,
          setVideoCuts,
          setPlayerTime
        );
      } else if (prev.action === 'textChange') {
        undoChangedText(
          prev,
          wordsArr,
          setCurrentSelection,
          setShouldRerenderSub,
          setCurrentWordIndex,
          subList,
          setPlayerTime
        );
      }
    },
    [
      setCurrentWordIndex,
      setCurrentSelection,
      setVideoCuts,
      setPlayerTime,
      setShouldRerenderSub,
      subList,
    ]
  );

  useEffect(() => {
    if (state.shouldUndo) {
      dispatch({ type: 'setShouldUndo', data: false });
      let prev = undoHistory.pop();
      if (prev && prev.type === 'transcription' && words) {
        if (videoRef.current) videoRef.current.pause();
        handleTextUndo(prev, words);
      }
    }
  }, [words, state.shouldUndo, dispatch, handleTextUndo, videoRef]);

  return { undoHistory, updateUndoHistory };
};

export default useUndo;
