import { useEffect, useState } from 'react';
import { fabric } from 'fabric';
import { checkResizeOffScreen } from '../../../utils/checkOffScreen';

const newVideo = (video, speaker, id) => {
  return new fabric.Video(video, {
    id,
    src: video.src,
    originalWidth: video.videoWidth,
    originalHeight: video.videoHeight,
    left: speaker.x,
    top: speaker.y,
    width: speaker.w,
    height: speaker.h,
    stroke: 'rgba(255,255,255,1)',
    cropRect: { ...speaker },
  });
}

const useAddSpeakers = (
  video,
  canvas,
  speakers,
  objectIdCount,
  setObjectIdCount,
  isCanvasData,
  setSpeakerVideos
) => {
  const [isFirtLoad, setIsFirtLoad] = useState(true);

  useEffect(() => {
    if (isFirtLoad && !isCanvasData && video && canvas) {
      setIsFirtLoad(false);
      let videos = [];
      let lastId = objectIdCount;
      if (speakers.length > 0) {
        for (let i = 0; i < speakers.length; i++) {
          let speaker = speakers[i];
          let speakerVideo = newVideo(video, speaker, lastId);
          canvas.add(speakerVideo);
          lastId++;
          checkResizeOffScreen(speakerVideo, canvas, null);
          videos.push(speakerVideo);
        }
      } else {
        let speakerVideo = newVideo(
          video,
          { w: video.videoWidth, h: video.videoHeight, x: 0, y: 0 },
          lastId
        );
        canvas.add(speakerVideo);
        lastId++;
        checkResizeOffScreen(speakerVideo, canvas, null);
        videos.push(speakerVideo);
      }
      setSpeakerVideos(videos);
      setObjectIdCount(lastId);
    }
  }, [
    isCanvasData,
    objectIdCount,
    setObjectIdCount,
    video,
    canvas,
    isFirtLoad,
    speakers,
    setSpeakerVideos,
  ]);
};

export default useAddSpeakers;
