import {
  findCurrentWordIndex,
  handleVideoCuts,
} from '../../../utils/updateTextAndCuts';
import { updateSubText } from '../../../utils/updateSubtitlesText';
import { findSubWithWordIndex } from '../../../utils/findIndex';

const finishUndo = (
  wordsArr,
  setCuts,
  setPlayerTime,
  index,
  setWordIndex,
  setCurrentSelection
) => {
  handleVideoCuts('delete', wordsArr, setCuts, setPlayerTime, index);
  setCurrentSelection(null);
  let selection = document.getSelection();
  selection.removeAllRanges();
  setWordIndex(index);
};

export const undoDeletedText = (
  target,
  wordsArr,
  setWordIndex,
  setCurrentSelection,
  setCuts,
  setPlayerTime
) => {
  for (let i = 0; i < target.length; i++) {
    let word = wordsArr[target[i]];
    word.active = true;
    if (word.next !== null) word.next = null;
  }
  finishUndo(
    wordsArr,
    setCuts,
    setPlayerTime,
    target[0],
    setWordIndex,
    setCurrentSelection
  );
};

export const undoRestoredText = (
  target,
  wordsArr,
  setWordIndex,
  setCurrentSelection,
  setCuts,
  setPlayerTime
) => {
  for (let i = 0; i < target.length; i++) {
    let word = wordsArr[target[i]];
    word.active = false;
  }
  let currentIndex = findCurrentWordIndex(target[0], wordsArr);
  finishUndo(
    wordsArr,
    setCuts,
    setPlayerTime,
    currentIndex,
    setWordIndex,
    setCurrentSelection
  );
};

export const undoChangedText = (
  prev,
  wordsArr,
  setCurrentSelection,
  setShouldRerenderSub,
  setWordIndex,
  subList,
  setPlayerTime
) => {
  let word = wordsArr[prev.target];
  let sub = findSubWithWordIndex(prev.target, wordsArr, subList);
  if (sub) {
    let text = prev.change.value.length > 0 ? prev.change.value : null;
    updateSubText(word._id, text, word.silence, setShouldRerenderSub, sub);
  }
  word.silence = prev.change.silence;
  word.text = prev.change.value;
  setCurrentSelection(null);
  let selection = document.getSelection();
  selection.removeAllRanges();
  setPlayerTime(prev.target)
  setWordIndex(prev.target);
};
