import React from 'react';
import styles from './TemplateList.module.css';
import { useHistory } from 'react-router-dom';
import Template from './Template/Template';

const TemplateList = ({ current, templates, dispatch }) => {
  const history = useHistory();
  const templateClickHandler = (template) => {
    dispatch({ type: 'setSelectedTemplate', data: { ...template } });
    history.push('/editor/new');
  };

  return (
    <div className={styles.TemplateList}>
      <h2 className={styles.Heading}>{current}</h2>
      <p className={styles.Subheading}>
        Make your vlog, webinar or intervew video look professional
      </p>
      <div className={styles.List}>
        {templates.commonTemplate
          .filter((template) => template.resize === current)
          .map((template) => (
            <Template
              key={template._id}
              template={template}
              clickHandler={templateClickHandler}
            />
          ))}
      </div>
    </div>
  );
};

export default TemplateList;
