import { useCallback, useEffect, useState, useRef, useContext } from 'react';
import EventContext from '../../../contexts/EventContext';
import {
  onCreated,
  onModifying,
  onModified,
  onCleared,
  onUpdated,
  onTextEnter,
  onTextExit,
  onAdded,
  onRemoved,
} from '../utils/onCanvasEvents';

const removeListeners = (c) => {
  c.off('selection:created');
  c.off('selection:updated');
  c.off('selection:cleared');
  c.off('object:modified');
  c.off('object:added');
  c.off('object:removed');
  c.off('object:rotating');
  c.off('object:scaling');
  c.off('text:editing:entered');
  c.off('text:editing:exited');
};

const useSelectionObserver = (canvas, state, dispatch, fabricSub) => {
  const { eventState, eventDispatch } = useContext(EventContext);
  const [isModifying, setIsModifying] = useState(false);
  const isFirstLoad = useRef(true);
  const currentId = useRef(null);

  const addListeners = useCallback(
    (c) => {
      c.on('text:editing:entered', (e) =>
        onTextEnter(e, c, setIsModifying, dispatch)
      );
      c.on('text:editing:exited', (e) =>
        onTextExit(e, c, setIsModifying, dispatch)
      );
      c.on('selection:created', (e) => onCreated(e, dispatch));
      c.on('selection:updated', (e) => onUpdated(e, dispatch));
      c.on('object:modified', (e) =>
        onModified(e, c, dispatch, eventDispatch, setIsModifying)
      );
      c.on('object:added', (e) => onAdded(e, dispatch, c, fabricSub));
      c.on('object:removed', (e) => onRemoved(e, dispatch));
      c.on('selection:cleared', (e) => onCleared(e, c, dispatch, currentId));
      c.on('object:rotating', () => onModifying(setIsModifying, dispatch));
      c.on('object:scaling', () => onModifying(setIsModifying, dispatch));
    },
    [dispatch, eventDispatch, fabricSub]
  );

  useEffect(() => {
    if (canvas) addListeners(canvas);
    return () => {
      if (canvas) removeListeners(canvas);
    };
  }, [canvas, addListeners]);

  useEffect(() => {
    if (!isFirstLoad.current) {
      let data = true;
      if (
        isModifying ||
        eventState.isMoving ||
        !state.currentCoords ||
        state.isCropMode ||
        state.currentObject.type === 'activeSelection'
      ) {
        data = false;
      }
      dispatch({ type: 'setShowToolbar', data });
    }
  }, [
    eventState,
    isModifying,
    state.isCropMode,
    state.currentCoords,
    state.currentObject,
    dispatch,
  ]);

  useEffect(() => {
    if (!isFirstLoad.current) {
      if (
        !state.currentCoords ||
        state.currentObject.type === 'activeSelection'
      ) {
        dispatch({ type: 'setShowToolbar', data: false });
      } else if (
        state.currentObject.object &&
        state.currentObject.object.id !== currentId.current
      ) {
        currentId.current = state.currentObject.object.id;
        dispatch({ type: 'setShowToolbar', data: true });
      }
    } else isFirstLoad.current = false;
  }, [state.currentCoords, state.currentObject, dispatch, state.isCropMode]);
};

export default useSelectionObserver;
