const handleDeletedCuts = (
  wordsArr,
  setCuts,
  setPlayerTime,
  wordIndex
) => {
  let arr = [];
  let isLastDeleted = false;
  let cutStart = 0;
  let cutStartIndex = 0;
  let cutEndIndex = 0;
  let cutEnd = 0;
  for (let i = 0; i < wordsArr.length; i++) {
    let word = wordsArr[i];
    if (word.active && isLastDeleted) {
      isLastDeleted = false;
      wordsArr[cutStartIndex].next = i;
      arr.push({ start: cutStart, end: cutEnd });
    } else if (!word.active && !isLastDeleted) {
      isLastDeleted = true;
      cutStart = word.start;
      cutStartIndex = i;
      cutEnd = word.end;
    } else if (!word.active && isLastDeleted) {
      cutEnd = word.end;
      cutEndIndex = i;
    }
  }

  if (isLastDeleted) arr.push({ start: cutStart, end: cutEnd });

  if (isLastDeleted && cutStartIndex !== wordsArr.length - 1) {
    wordsArr[cutStartIndex].next = cutEndIndex;
  } else if (isLastDeleted) {
    wordsArr[cutStartIndex].next = wordsArr.length - 1;
  }
  if (wordIndex !== null) setPlayerTime(wordIndex, arr);
  setCuts([...arr]);
};

const handleRestoredCuts = (
  setCuts,
  setPlayerTime,
  wordIndex,
  restoreStart
) => {
  setCuts((prevState) => {
    let newState = [];
    for (let i = 0; i < prevState.length; i++) {
      let cut = prevState[i];
      if (cut.start !== restoreStart) newState.push(cut);
    }
    setPlayerTime(wordIndex);
    return [...newState];
  });
};

export const handleVideoCuts = (
  type,
  wordsArr,
  setCuts,
  setPlayerTime,
  wordIndex,
  restoreStart
) => {
  if (type === 'delete') {
    handleDeletedCuts(wordsArr, setCuts, setPlayerTime, wordIndex);
  } else if (type === 'restore')
    handleRestoredCuts(setCuts, setPlayerTime, wordIndex, restoreStart);
};

export const findCurrentWordIndex = (deletedIndex, wordsArr) => {
  let currentIndex = null;
  for (let i = deletedIndex; i >= 0; i--) {
    let word = wordsArr[i];
    if (word.active) {
      currentIndex = i;
      break;
    }
  }

  if (currentIndex === null) {
    for (let i = deletedIndex; i < wordsArr.length; i++) {
      let word = wordsArr[i];
      if (word.active) {
        currentIndex = i;
        break;
      }
    }
  }
  return currentIndex;
};

export const deleteWord = (
  wordsArr,
  wordIndex,
  setWordIndex,
  setCuts,
  setPlayerTime,
  updateUndoHistory
) => {
  let word = wordsArr[wordIndex];
  word.active = false;
  updateUndoHistory({
    type: 'transcription',
    action: 'delete',
    target: [wordIndex],
  });
  let currentIndex = findCurrentWordIndex(wordIndex, wordsArr);
  setWordIndex(currentIndex);
  handleVideoCuts('delete', wordsArr, setCuts, setPlayerTime, currentIndex);
};

export const deleteSelection = (
  wordsArr,
  setWordIndex,
  currentSelection,
  setCurrentSelection,
  setCuts,
  setPlayerTime,
  updateUndoHistory
) => {
  let deletedStartIndex = currentSelection.start;
  let deleteEndIndex = currentSelection.end;
  let deletedIndexArr = [];

  for (let i = deletedStartIndex; i < wordsArr.length; i++) {
    let word = wordsArr[i];
    if (word.active) deletedIndexArr.push(i);
    word.active = false;
    if (i === deleteEndIndex) break;
  }

  updateUndoHistory({
    type: 'transcription',
    action: 'delete',
    target: [...deletedIndexArr],
  });

  let currentIndex = findCurrentWordIndex(deletedStartIndex, wordsArr);
  setWordIndex(currentIndex);
  handleVideoCuts('delete', wordsArr, setCuts, setPlayerTime, currentIndex);
  setCurrentSelection(null);
};

export const restoreDeleted = (
  wordsArr,
  wordIndex,
  setWordIndex,
  setCurrentSelection,
  setCuts,
  setPlayerTime,
  updateUndoHistory
) => {
  let resotreIndexArr = [];
  for (let i = wordIndex; i < wordsArr.length; i++) {
    let word = wordsArr[i];
    if (!word.active) {
      word.active = true;
      resotreIndexArr.push(i);
      if (word.next !== null) word.next = null;
    } else break;
  }
  updateUndoHistory({
    type: 'transcription',
    action: 'restore',
    target: [...resotreIndexArr],
  });

  let restoreStart = wordsArr[wordIndex].start;
  handleVideoCuts(
    'restore',
    null,
    setCuts,
    setPlayerTime,
    wordIndex,
    restoreStart
  );
  setCurrentSelection(null);
  let selection = document.getSelection();
  selection.removeAllRanges();
  setWordIndex(wordIndex);
};
