const handleNoSilenceAndText = (id, text, currentSub) => {
  let words = currentSub.val.words;
  for (let i = 0; i < words.length; i++) {
    let word = words[i];
    if (word._id === id) {
      let isWhitespace = word.text.indexOf(' ') === 0;
      word.text = isWhitespace ? ' ' + text : text;
      if (word.silence) word.silence = false;
      break;
    }
  }
};

const handleSilenceAndText = (id, text, currentSub) => {
  let words = currentSub.val.words;
  if (words) {
    for (let i = 0; i < words.length; i++) {
      let word = words[i];
      if (word._id === id) {
        let isFirst = i === 0;
        text = isFirst ? text + ' ' : text;
        let isNoWhitespace = word.text.indexOf(' ') !== 0;
        word.text = isNoWhitespace && !isFirst ? ' ' + text : text;
        if (word.silence) word.silence = false;
        break;
      }
    }
  } else {
    currentSub.val.silence = false;
    currentSub.val.words = [{ ...currentSub.val, text }];
  }
};

const handleNoText = (id, currentSub) => {
  let words = currentSub.val.words;
  let silenceCount = 0;
  for (let i = 0; i < words.length; i++) {
    let word = words[i];
    if (word._id === id) {
      word.text = '';
      word.silence = true;
    }
    if (word.silence) silenceCount++;
  }
  if (silenceCount === words.length) {
    currentSub.val.silence = true;
    currentSub.val.words = null;
  }
};

export const updateSubText = (
  id,
  text,
  isSilence,
  setShouldRerenderSub,
  currentSub
) => {
  if (!isSilence && text && id) handleNoSilenceAndText(id, text, currentSub);
  else if (text && isSilence) handleSilenceAndText(id, text, currentSub);
  else if (id) handleNoText(id, currentSub);
  setShouldRerenderSub(true);
};
