import React from 'react';
import { NavLink, useRouteMatch, useLocation } from 'react-router-dom';
import styles from './SideDrawer.module.css';
import User from './User/User';
import { ReactComponent as Templates } from '../../../assets/homepage/templates.svg';
import { ReactComponent as Projects } from '../../../assets/homepage/projects.svg';
import { ReactComponent as Settings } from '../../../assets/homepage/settings.svg';

const SideDrawer = () => {
  const match = useRouteMatch();
  const location = useLocation();
  
  return (
    <div className={styles.SideDrawer}>
      <User />
      <div className={styles.Links}>
        <NavLink
          to={`${match.path}/templates`}
          className={styles.Link}
          activeClassName={styles.Active}
          replace
        >
          <Templates
            fill={
              location.pathname.includes('templates') ? '#6F7BD0' : '#999999'
            }
          />
          <span className={styles.LinkText}>Templates</span>
        </NavLink>
        <NavLink
          to={`${match.path}/projects`}
          className={styles.Link}
          activeClassName={styles.Active}
          replace
        >
          <Projects
            fill={
              location.pathname.includes('projects') ? '#6F7BD0' : '#999999'
            }
          />
          <span className={styles.LinkText}>My projects</span>
        </NavLink>
        <NavLink
          to={`${match.path}/settings`}
          className={styles.Link}
          activeClassName={styles.Active}
          replace
        >
          <Settings
            fill={
              location.pathname.includes('settings') ? '#6F7BD0' : '#999999'
            }
          />
          <span className={styles.LinkText}>Settings</span>
        </NavLink>
      </div>
    </div>
  );
};

export default SideDrawer;
